//Inicar valor del state.
const initialState = {
	login: {
		loading: false,
		inputs: {
			user: '',
			password: '',
			checkbox: true,
		},
	},
};

export default (state = initialState, { type, payload }) => {
	switch (type) {
		case 'UPDATE_FORM': {
			const { form, loading, inputs } = payload;

			return {
				...state,
				[form]: {
					loading,
					inputs,
				},
			};
		}

		default: {
			return state;
		}
	}
};

